.table-head th{
  border: 1px solid black;
  padding: 4px 6px;
}
.Checked{
  background: #00df68;
}
.Checking{
  background: #fd79a8;
}
.InProgress{
  background: #b2bec3;
}
.student{
  border: 1px solid black;
}
.student td{
  border: 1px solid black;
  text-align: center;
}

.no-mark{
  background: red;
}
.mark{
  background: green;
}
.score-table{
  border: 1px solid black;
  width: 90%;
  margin: 1rem auto;
  border-collapse: collapse;
}
